"use client";
import Image from "next/image";
import Link from "next/link";
import { motion, AnimatePresence } from "framer-motion";
import SearchFilter from "@/components/ui/SearchFilter";
import Slider from "../home/Slider";
import { Stars, MoveRight, CalendarDays, Ticket } from "lucide-react";

export default function HeroSection() {
    return (
        <section className="relative h-[80vh] min-h-[600px] overflow-hidden">
            {/* Animated Background Elements */}
            <div className="absolute inset-0">
                <motion.div
                    animate={{
                        rotate: [0, 360],
                        scale: [1, 1.2, 1],
                    }}
                    transition={{
                        duration: 20,
                        repeat: Infinity,
                        ease: "linear",
                    }}
                    className="absolute -left-[10%] -top-[10%] w-[40%] h-[40%] rounded-full bg-gradient-to-r from-purple-600/30 to-blue-600/30 blur-xl"
                />

                <motion.div
                    animate={{
                        rotate: [360, 0],
                        scale: [1, 1.3, 1],
                    }}
                    transition={{
                        duration: 25,
                        repeat: Infinity,
                        ease: "linear",
                    }}
                    className="absolute -right-[15%] -bottom-[15%] w-[50%] h-[50%] rounded-full bg-gradient-to-r from-purple-600/30 to-pink-600/30 blur-xl"
                />
            </div>

            {/* Main Content */}
            <div className="relative h-full w-full flex items-center">
                {/* Background Image with Gradient Overlay */}
                <div className="absolute inset-0 z-0">
                    <Image
                        src="/concert-bg.jpg"
                        alt="Concert Background"
                        fill
                        className="object-cover"
                        priority
                    />
                    <div className="absolute inset-0 bg-gradient-to-r from-purple-900/60 to-blue-900/40" />
                </div>

                <AnimatePresence>
                    {/* Content Container */}
                    <motion.div
                        initial={{ opacity: 0, x: -50 }}
                        animate={{ opacity: 1, x: 0 }}
                        transition={{ duration: 0.8 }}
                        className="relative z-10 container mx-auto px-4 text-left"
                    >
                        {/* Headline Section */}
                        <div className="max-w-2xl space-y-6">
                            <motion.div
                                initial={{ opacity: 0 }}
                                animate={{ opacity: 1 }}
                                transition={{ delay: 0.3 }}
                                className="flex items-center gap-2 text-blue-200 mb-4"
                            >
                                <Stars className="h-6 w-6" />
                                <span className="text-sm font-semibold tracking-wide">
                                    PREMIUM EVENT EXPERIENCE
                                </span>
                            </motion.div>

                            <motion.h1
                                initial={{ y: 20, opacity: 0 }}
                                animate={{ y: 0, opacity: 1 }}
                                transition={{ delay: 0.5 }}
                                className="text-4xl md:text-6xl font-bold text-white leading-tight"
                            >
                                Create Unforgettable
                                <span className="bg-gradient-to-r from-purple-400 to-pink-400 bg-clip-text text-transparent">
                                    {" "}Event Experiences
                                </span>
                            </motion.h1>

                            <motion.p
                                initial={{ opacity: 0 }}
                                animate={{ opacity: 1 }}
                                transition={{ delay: 0.7 }}
                                className="text-lg md:text-xl text-gray-200 max-w-xl"
                            >
                                Discover and organize the most exciting university events. From concerts to workshops - we make event planning magical.
                            </motion.p>

                            {/* CTA Buttons */}
                            <motion.div
                                initial={{ opacity: 0 }}
                                animate={{ opacity: 1 }}
                                transition={{ delay: 0.9 }}
                                className="flex gap-4 mt-8"
                            >
                                <Link href="/events"
                                    className="flex items-center px-8 py-4 bg-purple-600 hover:bg-purple-700 rounded-full text-white transition-all duration-300 group"
                                >
                                    <Ticket className="h-5 w-5 mr-2" />
                                    Explore Events
                                    <MoveRight className="h-5 w-5 ml-2 group-hover:translate-x-1 transition-transform" />
                                </Link>

                                <Link
                                    href="/create-event"
                                    className="flex items-center px-8 py-4 bg-white/10 hover:bg-white/20 backdrop-blur-sm rounded-full text-white transition-all duration-300 group"
                                >
                                    <CalendarDays className="h-5 w-5 mr-2" />
                                    Create Event
                                </Link>
                            </motion.div>
                        </div>
                    </motion.div>
                </AnimatePresence >

                {/* Search Filter */}
                < motion.div
                    initial={{ opacity: 0, y: 50 }
                    }
                    animate={{ opacity: 1, y: 0 }}
                    transition={{ delay: 1.2 }}
                    className="absolute bottom-8 left-0 right-0 z-20"
                >
                    <SearchFilter className="mx-auto bg-white/90 backdrop-blur-sm shadow-xl rounded-full" />
                </motion.div >
            </div >

            {/* Slider Component */}
            < motion.div
                initial={{ opacity: 0 }}
                animate={{ opacity: 1 }}
                transition={{ delay: 1.5 }}
                className="absolute bottom-0 right-0 w-[40%] h-full z-0"
            >
                <Slider />
            </motion.div >
        </section >
    );
}